<template>
  <b-row>
    <b-col md="6">
      <b-card>
        <strong slot="header">Offer </strong>
        <b-button slot="footer" @click="handleSubmit" variant="primary" :disabled="submitting" type="submit">
          <i class="fa fa-dot-circle-o"></i> Submit
        </b-button>

        <b-form>
          <b-form-group label-cols-md="3" label="Name of offer" label-for="offer-name">
            <b-form-input
              v-validate="'required'"
              name="name"
              v-model="offer.name"
              type="text"
              placeholder="Name of the offer"
            ></b-form-input>
            <span class="alert-danger">{{ errors.first('name') }}</span>
          </b-form-group>

          <!-- <b-form-group label-cols-md="3" label="Type" label-for="type">
                        <b-form-select  id="type" v-model="selected" :options="options" ></b-form-select>
          </b-form-group>-->

          <b-form-group label-cols-md="3" label="Start Date" label-for="s_date">
            <b-form-input
              v-model="offer.start_date"
              v-validate="'required|notPast'"
              name="start_date"
              type="date"
            ></b-form-input>
            <span class="alert-danger">{{ errors.first('start_date') }}</span>
          </b-form-group>

          <b-form-group label-cols-md="3" label="End Date">
            <b-form-input
              v-model="offer.end_date"
              v-validate="'required|earlier'"
              name="end_date"
              type="date"
            ></b-form-input>
            <span class="alert-danger">{{ errors.first('end_date') }}</span>
          </b-form-group>

          <b-form-group label-cols-md="3" label="Select Image" label-for="image">
            <b-form-file
            v-validate="'required'"
              accept="image/jpeg"
              ref="image"
              name="file"
              v-model="file"
              :state="Boolean(file)"
              placeholder="Choose a file..."
              drop-placeholder="Drop file here..."
            ></b-form-file>
            <span class="alert-danger">{{ errors.first('file') }}</span>
          </b-form-group>

          <b-form-group label-cols-md="3" label="Caption" label-for="caption">
            <b-form-input
              v-validate="'required'"
              v-model="offer.caption"
              name="caption"
              type="text"

              placeholder="Image caption"
            ></b-form-input>
            <span class="alert-danger">{{ errors.first('caption') }}</span>
          </b-form-group>
        </b-form>
         <b-form-group v-if="uploadPercentage" label-cols-md="3" >
              <b-progress  :value="uploadPercentage" variant="info" key="info"></b-progress>
         </b-form-group>
          <b-form-group v-if="rqst_err" label-cols-md="3" >
            <span class="alert-danger">{{ rqst_err }}</span>
          </b-form-group>
      </b-card>

    </b-col>
  </b-row>
  <!--/.row-->
</template>


<script>
import { mapState, mapActions } from "vuex";
import { router } from "../../router";
import axios from "axios";
import { authHeader } from "../../_auth";
export default {
  name: "CreateOffer",
  components: {},

  data: () => {
    return {
      file: null,
      selected: null,
      submitting: false,
      submitted: false,
      rqst_err: "",
      uploadPercentage: 0
    };
  },
  methods: {
    ...mapActions("offer", ["createOffer"]),
    handleSubmit(e) {
      this.$validator.validate().then(valid => {
        if (valid) {
          this.submitting = true;
          this.rqst_err = ""
          var headers = authHeader();
          headers["content-type"] = "multipart/form-data";
          const config = { 
            headers: headers,
            onUploadProgress: function( progressEvent ) {
              this.uploadPercentage = parseInt( Math.round( ( progressEvent.loaded * 100 ) / progressEvent.total ) );
              }.bind(this)
            };

          const fd = new FormData();
          fd.append("file", this.file, this.file.name);
          fd.append("aspect", "9x16");
          fd.append("aspect_override", "true");
          fd.append("media_type", "image");
          // Offer portal specifics
          fd.append("use_template", "true");
          fd.append("template_name", "second.html");
          const API_HOST = process.env.VUE_APP_MEDIA_API_HOST
          axios
            .post(
              `${API_HOST}ig-media-uploader/`,
              fd,
              config
            )
            .then(res => {
              if( !this.submitted){
                this.createOffer({ ...res.data, ...this.offer });
                this.submitted = true;
              }
            
            })
            .catch(error => {
              this.submitting = false;
              this.rqst_err = error.response.data.msg;
              this.uploadPercentage = 0;
              if(error.response.status == 400){
                router.push('login');
              }
              
            })
        }
      });
    }
  },
  computed: {
    ...mapState("offer", ["offer"])
  },
    created() {
      let self = this;
    this.$validator.extend('earlier', {
      getMessage(field, val) {
        return 'should be later than "start date"'
      },
      validate(value, field) {
        let startParts = self.offer.start_date.split('-')
        let endParts = value.split('-')
        let start = new Date(startParts[0], startParts[1] -1, startParts[2]) // month is 0-based
        let end = new Date(endParts[0], endParts[1] -1, endParts[2])
        return end > start
      }
    });
      this.$validator.extend('notPast', {
      getMessage(field, val) {
        return 'should not be in past'
      },
      validate(value, field) {
        let startParts = self.offer.start_date.split('-')       
        let start = new Date(startParts[0], startParts[1] -1, startParts[2]) // month is 0-based  
        let today = new Date()
        today.setHours(0,0,0,0);
        return start >= today
      }
    });
  },
};




  // methods: {
